.home {
    position:fixed;
    width: 100%;
    height: 100%;
}

.description {
    top: 66vh;
    position: fixed;
    width: 100%;
    height: 30%;

    font-family: 'Joy Multiplication', sans-serif;
    letter-spacing: 1.5px;
    line-height: 20px;
    color:#ffff00;
    filter: drop-shadow(5px 5px 7px rgba(0, 0, 0, 0.7));
    z-index: 1;
}

.iconContainer {
    display: flex; 
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 70%;
    height: 30%;
    position: fixed;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
}

.homeTextArea {
    justify-content: center;
    align-items: center;
    display: flex; 
    flex-wrap:wrap;
    position: absolute;
    width: 100%;
    height: 30%;

    font-family: 'Neuropol', sans-serif;

    color:#ffff00;
    font-size: 2em;
    text-align: center;
    filter: drop-shadow(5px 5px 7px rgba(0, 0, 0, 0.7));
    z-index: 1;
}

.textArea.fadeIn {
    animation-fill-mode: forwards;
    animation: taFadeIn 0.5s;
    opacity: 1;
    transform: scale(1);
    color: #ffff00;
}

@keyframes taFadeIn {
    0% { transform: scale(0.8)
    }
    100% { transform: scale(1)}
}

